body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #00c6ff, #0072ff);
  color: white;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  font-family: 'Arial', sans-serif;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.centered-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}

button {
  background-color: gray;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: not-allowed;
  margin-top: 20px;
}

button.active {
  background-color: green;
  cursor: pointer;
}

.green-box {
  background-color: lightgreen;
  padding: 20px;
  margin-top: 20px;
  border-radius: 5px;
  max-width: 80%;
  word-wrap: break-word;
}

.resizable-container {
  background-color: lightgreen;
  padding: 20px;
  margin-top: 20px;
  border-radius: 5px;
  overflow-y: scroll;
  max-height: 400px;
  position: relative;
  max-width: 80%;
  word-wrap: break-word;
}

.download-button {
  position: absolute;
  top: 10px;
  right: 10px;
}
